@import (less) "color.less";

.tabs-container {
  display: flex;
  .disabledTab {
    pointer-events: none;
    cursor: not-allowed;
    opacity: 0.5;
    background-color: #d6d6d6;
  }
  .enabledTab {
    pointer-events: auto;
    background-color: @dark_white;
    cursor: pointer;
    opacity: 1;
  }
  .tab-container {
    height: 5.4375em;
    border-right: 0.0625em solid @light_gray;
    text-align: center;
    flex-grow: 1;
    flex-basis: 0;
    padding: 0.625em;
    &:last-of-type {
      border: 0;
    }

    &.selected {
      background-color: @white;
      height: 5.375em;
      i {
        color: @green;
      }

      .tab-title {
        color: @green;
      }
    }

    i {
      display: block;
      font-size: 1.5em;
      color: @gray;
    }

    .tab-title {
      text-transform: uppercase;
      color: @gray;
      font-size: 0.75em;
      display: inline-block;
      line-height: 1;
      font-weight: 600;
      margin-top: 0.625em;
      margin-bottom: 0.625em;
    }
  }
}

